* {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

#map {
  width: 100%;
  height: 100vh;
}

.info {
  background: #fffc;
  border-radius: 5px;
  padding: 6px 8px;
  font: 14px / 16px Arial, Helvetica, sans-serif;
  box-shadow: 0 0 15px #0003;
}

.info h4 {
  color: #777;
  margin: 0 0 5px;
}

.legend {
  color: #555;
  line-height: 18px;
}

.legend i {
  float: left;
  opacity: .7;
  width: 18px;
  height: 18px;
  margin-right: 8px;
}

.custom-button {
  color: #007bff;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #007bff;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 14px;
}

.custom-button:hover {
  color: #fff;
  background-color: #007bff;
}
